import React, { Component } from "react";
import rwanda_highlights from "./images/rwanda_highlights.jpg";
import sergeyzolkin from "./images/sergeyzolkin.jpg";

class Portfolio extends Component {
  render() {
    return (
      <article className="projects-section">
        <article className="projects-grid">
          <label>
            <img
              src={rwanda_highlights}
              className="project-image"
              alt="kigal-cc"
            />
            <a className="project-text" href="/Property_Pro_Lite">
              Property_Pro_Lite
            </a>
          </label>
          <label>
            <img
              src={sergeyzolkin}
              className="project-image"
              alt="project-cover"
            />
            <a className="project-text" href="/Property_Pro_Lite">
              Coming Soon
            </a>
          </label>
          <label>
            <img
              src={sergeyzolkin}
              className="project-image"
              alt="project-cover"
            />
            <a className="project-text" href="/Property_Pro_Lite">
              Coming Soon
            </a>
          </label>
          <label>
            <img
              src={sergeyzolkin}
              className="project-image"
              alt="project-cover"
            />
            <a className="project-text" href="/Property_Pro_Lite">
              Coming Soon
            </a>
          </label>
        </article>
      </article>
    );
  }
}

export default Portfolio;
