import React from "react";
import ReactDOM from "react-dom";
import { CardBody, CardFooter } from "react-simple-card";
import Picture1 from "./images/Picture1.png";
import sergeyzolkin from "./images/sergeyzolkin.jpg";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faTwitter,
  faLinkedin,
  faGithub,
  faFacebook,
  faInstagram
} from "@fortawesome/fontawesome-free-brands";
ReactDOM.render(<App />, document.getElementById("root"));

function App() {
  return (
    <article className="card-container">
      <article className="card-body">
        <article className="container-fluid">
          <figure className="col-xs-6">
            <img src={sergeyzolkin} className="card-picture" alt="second img" />
          </figure>
          <CardBody className="col-xs-6 side-front-content">
            <h4 id="name">Habineza Regis</h4>
            <h5 id="career">Frontend developer</h5>
            <form>
              <label htmlFor="name">
                Name*
                <input type="text" placeholder="Name" required=""></input>
              </label>
              <label htmlFor="email">
                Email*
                <input type="text" placeholder="email" required=""></input>
              </label>
              <label htmlFor="email">
                <textarea
                  type="text"
                  placeholder="text Regis what you think"
                  required=""
                ></textarea>
              </label>
              <br />
              <input type="button" value="Send" />
            </form>
          </CardBody>
        </article>
        <section className="card-side side-back">
          <article className="container-fluid">
            <h1>Let's get in touch!</h1>
            <footer>
              <CardFooter id="small-footer">
                <FontAwesomeIcon icon={faTwitter} id="follow-link" />
                <FontAwesomeIcon icon={faLinkedin} id="follow-link" />
                <FontAwesomeIcon icon={faGithub} id="follow-link" />
              </CardFooter>
            </footer>
          </article>
        </section>
      </article>
      <article className="card-body">
        <article className="container-fluid">
          <figure className="col-xs-6">
            <img src={Picture1} className="card-picture" alt="pfl-card" />
          </figure>
          <CardBody className="col-xs-6 side-front-content">
            <h4>Habineza Regis</h4>
            <h5>Frontend developer</h5>
            <article>
              <p>Email:rhabinregis@gmail.com</p>
              <p>Mobile:+250722030078</p>
              <p>Address:Kigali,Rwanda</p>
            </article>
          </CardBody>
        </article>
        <section className="card-side side-back">
          <article className="container-fluid">
            <h1>Let's get in touch!</h1>
            <footer>
              <CardFooter id="small-footer">
                <FontAwesomeIcon icon={faTwitter} id="follow-link" />
                <FontAwesomeIcon icon={faLinkedin} id="follow-link" />
                <FontAwesomeIcon icon={faGithub} id="follow-link" />
                <FontAwesomeIcon icon={faFacebook} id="follow-link" />
                <FontAwesomeIcon icon={faInstagram} id="follow-link" />
              </CardFooter>
            </footer>
          </article>
        </section>
      </article>
    </article>
  );
}

export default App;
