import React  from "react";
import { NavLink } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faTwitter,
  faLinkedin,
  faGithub,
  faFacebook
} from "@fortawesome/fontawesome-free-brands";
//import { faThumbsUp, } from '@fortawesome/free-solid-svg-icons';

function Navigation() {
  return (
    <nav className="navbar navbar-expand-lg navbar-light bg-light">
      <button
        className="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarTogglerDemo03"
        aria-controls="navbarTogglerDemo03"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span className="navbar-toggler-icon"></span>
      </button>
      <NavLink className="navbar-brand" to="/">
        Habineza Regis
      </NavLink>

      <header className="collapse navbar-collapse" id="navbarTogglerDemo03">
        <ul className="navbar-nav mr-auto mt-2 mt-lg-0">
          <li className="nav-item active">
            <NavLink className="nav-link" to="/Resume">
              Resume <span className="sr-only">(current)</span>
            </NavLink>
          </li>
          <li className="nav-item">
            <NavLink className="nav-link" to="/Contact">
              Contact
            </NavLink>
          </li>
          <li className="nav-item">
            <NavLink className="nav-link " 
              to="/Portfolio"
            >
              Projects
            </NavLink>
          </li>
        </ul>
        <article >
          <a href="https://twitter.com/kellyrhabin">
          <FontAwesomeIcon icon={faTwitter} id="link-twitter"/>
          </a>
          <a href="https://www.linkedin.com/in/regis-habineza-3627aaa5/">
          <FontAwesomeIcon icon={faLinkedin} id="link-linkedin"/></a>
          <a href="https://github.com/RegisAndela?tab=repositories">
          <FontAwesomeIcon icon={faGithub} id="link-github"/></a>
          <a href="https://www.facebook.com/rhabin.regis">
          <FontAwesomeIcon icon={faFacebook} id="link-facebook"/></a>
        </article>
      </header>

    </nav>
  );
}

export default Navigation;
